.product {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: calc(100% - 220px);
  padding: 40px;
  overflow: auto;
  .product-table::-webkit-scrollbar {
    width: 10px;
    height: 10px;
    background-color: #f5f5f5;
  }
  .product-table::-webkit-scrollbar-thumb {
    border-radius: 5px;
    background-color: #f78a29;
  }
  .product-table {
    width: 100%;
    height: 100%;
    padding: 60px 40px;
    background-color: white;
    border-radius: 5px;
    overflow: auto;
  }
  .product-header {
    padding-bottom: 50px;
    display: flex;
    align-items: center;
    font-family: 'Roboto', sans-serif;
    @media only screen and (max-width: 1300px) {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
    }
    .product-item1 {
      display: flex;
      flex-wrap: nowrap;
      padding-right: 20px;
      @media only screen and (max-width: 1300px) {
        padding-bottom: 10px;
      }
      input {
        width: 230px;
        border: 1px solid #333333;
        border-radius: 10px;
        margin-right: 20px;
        padding: 5px 12px;
        font-size: 14px;
        height: 40px;
      }
    }
    .product-item2 {
      display: flex;
      margin-left: auto;
      align-items: center;
      @media only screen and (max-width: 1300px) {
        width: 100%;
        min-width: 600px;
        padding-right: 20px;
      }
      .drop-product {
        background-color: white;
        font-size: 14px;
        border: 1px solid #333333;
        border-radius: 10px;
        cursor: pointer;
        width: 180px;
        padding: 5px 12px;
        text-align: left;
        height: 40px;
        margin-right: 40px;
      }

      select {
        appearance: none;
        background: url('../../../img/site_images/dropdown.svg');
        background-repeat: no-repeat;
        background-position: 95%;
        border: 1px solid #333333;
      }
    }
  }
  .table {
    table {
      font-size: 14px;
      border-collapse: collapse;
      width: 100%;
      th {
        font-family: 'Roboto', sans-serif;
        font-weight: 700;
        font-size: 16px;
        border-bottom: 1px solid #c4c4c4;
      }
      td {
        height: 80px;
        font-weight: 600;
        vertical-align: middle;
        border-bottom: 1px solid #c4c4c4;
        border-top: 1px solid #c4c4c4;
      }
      .first {
        padding-left: 30px;
      }
      .second {
        padding-left: 60px;
      }
      .third {
        padding-left: 30px;
      }
      .edit {
        color: #367bf5;
        text-decoration: underline;
        text-align: center;
        padding-left: 10px;
      }
    }
  }
}

.product-add {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  padding: 60px;
  overflow: auto;
  .add-product::-webkit-scrollbar {
    width: 10px;
    height: 10px;
    background-color: #f5f5f5;
  }
  .add-product::-webkit-scrollbar-thumb {
    border-radius: 5px;
    background-color: #f78a29;
  }
  .add-product {
    width: 100%;
    height: 100%;
    background-color: white;
    border-radius: 5px;
    padding: 40px 130px;
    overflow: auto;
    .product-form {
      display: flex;
      flex-direction: column;
      flex-wrap: nowrap;
      font-family: 'Roboto', sans-serif;
      .remarks {
        height: 90px;
      }
      .table-product {
        table {
          width: 100%;
          tr {
            border-bottom: 1px solid #dfe0eb;
            .first {
              width: 300px;
            }
            .second {
              text-align: center;
            }
            th {
              padding: 5px 20px;
              height: 30px;
              font-size: 16px;
              font-weight: 700;
              font-family: 'Roboto', sans-serif;
            }
            td {
              height: 50px;
              padding: 5px 20px;
              font-size: 14px;
            }
          }
        }
      }
      .product-img {
        display: flex;
        flex-direction: column;
        img {
          width: 40px;
          height: 40px;
          margin: -10px 0 0 40px;
        }
        label {
          font-size: 16px;
        }
      }
      input {
        width: 100%;
        height: 50px;
        border: 1px solid #e3e5e6;
        border-radius: 8px;
        min-width: 200px;
      }
      label {
        font-weight: 700;
        font-size: 16px;
        padding: 20px 0 10px 0;
        min-width: 300px;
      }
      .save {
        width: 160px;
        height: 42px;
        background-color: #f78a29;
        border: none;
        border-radius: 8px;
        color: white;
        font-weight: 700;
      }
    }
  }
}

.title {
  font-size: 25px;
  font-weight: 700;
  text-align: center;
  padding: 40px 0 30px 0;
  min-width: 200px;
}

.product-item {
  display: flex;
  flex-wrap: nowrap;
  .item1 {
    margin-right: 50px;
    width: 50%;
    min-width: 200px;
  }
  .item2 {
    margin-left: 50px;
    width: 50%;
    min-width: 200px;
  }
}

.vector {
  padding: 10px 5px;
  float: right;
}
