.top-bar {
  min-width: 400px;
  a:hover {
    text-decoration: none;
  }
  .icon-left {
    display: flex;
    .icon-menu {
      margin-right: 10px;
      width: 25px;
      height: 25px;
    }
  }
}
.cart {
  padding: 60px 150px;
  p {
    font-weight: 700;
    font-size: 25px;
    line-height: 31px;
    letter-spacing: 0.2px;
    color: #f78a29;
    margin: 40px 0;
  }
  .cart-table::-webkit-scrollbar {
    width: 10px;
    height: 10px;
    background-color: #f5f5f5;
  }
  .cart-table::-webkit-scrollbar-thumb {
    border-radius: 5px;
    background-color: #f78a29;
  }
  .cart-table {
    overflow: auto;
    table {
      width: 100%;
      min-width: 900px;
      text-align: center;
      thead {
        tr {
          th {
            padding: 20px;
          }
          .total {
            min-width: 200px;
          }
        }
      }
      .left {
        text-align: left;
      }
      tbody {
        .first {
          font-weight: 700;
          text-decoration: underline;
        }
        .second {
          font-weight: 700;
        }
        .edit {
          cursor: pointer;
        }
        td {
          vertical-align: middle;
          padding: 20px;
          input[type='number'] {
            -webkit-appearance: textfield;
            -moz-appearance: textfield;
            appearance: textfield;
          }

          input[type='number']::-webkit-inner-spin-button,
          input[type='number']::-webkit-outer-spin-button {
            -webkit-appearance: none;
          }
          .number-input {
            border: 0;
            display: inline-flex;
            .minus {
              border-right: none;
            }
            .plus {
              border-left: none;
            }
            button {
              outline: none;
              -webkit-appearance: none;
              background-color: transparent;
              border: 1px solid black;
              align-items: center;
              justify-content: center;
              width: 30px;
              height: 35px;
              cursor: pointer;
              margin: 0;
              position: relative;
            }
          }
          .number-input button:before,
          .number-input button:after {
            display: inline-block;
            position: absolute;
            content: '';
            width: 10px;
            height: 1px;
            background-color: #212121;
            transform: translate(-50%, -50%);
          }
          .number-input button.plus:after {
            transform: translate(-50%, -50%) rotate(90deg);
          }

          .number-input input[type='number'] {
            width: 50px;
            height: 35px;
            border: none;
            margin: 0;
            font-size: 16px;
            text-align: center;
            border-radius: 0;
            border: 1px solid black;
            border-left: none;
            border-right: none;
          }
          .icon-menu {
            display: flex;
            align-items: center;
          }
          img {
            margin-right: 20px;
            width: 100px;
            height: 100px;
          }
        }
      }
    }
  }
}
.footer {
  width: 100%;
  padding: 0 150px;
  display: flex;
  .btnb {
    margin-left: auto;
    min-width: 170px;
  }
  .btna {
    margin-right: 20px;
    min-width: 260px;
  }
  button {
    background: #ffffff;
    border: 1px solid #f78a29;
    box-sizing: border-box;
    border-radius: 20px;
    padding: 5px 10px;
    font-weight: 700;
    font-size: 18px;
    line-height: 23px;
    letter-spacing: 0.2px;

    color: #f78a29;
    img {
      width: 40px;
      height: 30px;
      margin: 0 10px;
    }
    a:hover {
      text-decoration: none;
    }
  }
}
