.outlet {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: calc(100% - 220px);
  padding: 40px;
  overflow: auto;
}
.outlet-table::-webkit-scrollbar {
  width: 10px;
  height: 10px;
  background-color: #f5f5f5;
}
.outlet-table::-webkit-scrollbar-thumb {
  border-radius: 5px;
  background-color: #f78a29;
}
.outlet-table {
  width: 100%;
  height: 100%;
  padding: 60px 40px;
  background-color: white;
  border-radius: 5px;
  overflow: auto;
  .table {
    table {
      font-size: 14px;
      border-collapse: collapse;
      width: 100%;
      th {
        font-family: 'Roboto', sans-serif;
        font-weight: 700;
        font-size: 16px;
        border-bottom: 1px solid #c4c4c4;
      }
      .first {
        padding-left: 20px;
      }
      .second {
        padding-left: 50px;
      }
      .third {
        padding-left: 20px;
      }
      td {
        height: 80px;
        font-weight: 600;
        vertical-align: middle;
        border-bottom: 1px solid #c4c4c4;
        border-top: 1px solid #c4c4c4;
      }
      .first {
        width: 500px;
      }
    }
  }

  .outlet-header {
    padding-bottom: 50px;
    display: flex;
    align-items: center;
    @media only screen and (max-width: 900px) {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
    }
    .outlet-item1 {
      display: flex;
      align-items: center;
      flex-wrap: nowrap;
      padding-right: 20px;
      input{
        height: 40px;
        width: 250px;
      }
      @media only screen and (max-width: 900px) {
        padding-bottom: 10px;
      }
    }
    .outlet-item2 {
      display: flex;
      margin-left: auto;
      align-items: center;
      @media only screen and (max-width: 900px) {
        width: 100%;
        min-width: 450px;
      }
    }
  }
}


input {
  width: 230px;
  border: 1px solid #333333;
  border-radius: 10px;
  margin-right: 20px;
  padding: 5px 12px;
  font-size: 14px;
}

select {
  width: 180px;
  height: 30px;
  border: 1px solid #333333;
  border-radius: 10px;
  margin-right: 60px;
}

.delete {
  color: #e10916;
  text-decoration: underline;
  font-weight: 700;
}
.outlet-add {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: calc(100% - 220px);
  padding: 60px;
  overflow: auto;
  .outlet-form::-webkit-scrollbar {
    width: 10px;
    height: 10px;
    background-color: #f5f5f5;
  }
  .outlet-form::-webkit-scrollbar-thumb {
    border-radius: 5px;
    background-color: #f78a29;
  }
  .outlet-form {
    width: 100%;
    height: 100%;
    background-color: white;
    border-radius: 5px;
    font-family: 'Roboto', sans-serif;
    padding: 40px 140px 80px;
    overflow: auto;
    .title {
      font-size: 25px;
      font-weight: 700;
      text-align: center;
      font-family: 'Mulish', sans-serif;
      padding: 0;
      min-width: 200px;
    }
    .form {
      padding: 0 40px 20px 40px;
      font-size: 16px;
      font-weight: 700;
      .add-outlet {
        display: flex;
        flex-wrap: nowrap;
        .add-left {
          margin-right: 50px;
          width: 450px;
          min-width: 200px;
          select {
            appearance: none;
            background: url('../../../img/site_images/select.svg');
            background-repeat: no-repeat;
            background-position: 95%;
            width: 100%;
            height: 50px;
            border: 1px solid #e3e5e6;
            padding: 10px;
          }
        }
        .add-right {
          margin-left: 50px;
          width: 450px;
          min-width: 200px;

          .hide {
            display: flex;
            flex-direction: column;
            visibility: hidden;
          }
        }
        input {
          width: 100%;
          height: 50px;
          border: 1px solid #e3e5e6;
          border-radius: 8px;
        }
        label {
          font-weight: 700;
          font-size: 16px;
          padding: 20px 0 10px 0;
          width:  300px;
        }
      }
    }
  }
}
.add-below {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  padding-top: 10px;
  .below-item {
    padding-top: 40px;
  }
  input {
    width: 100%;
    height: 60px;
    min-width: 0;
    border: 1px solid #e3e5e5;
    margin: 0;
    min-width: 400px;
  }
  label {
    padding: 20px 0 10px 0;
  }
}

.view {
  display: flex;
  padding-top: 40px;
  img {
    width: 30px;
    height: 30px;
    margin-right: 20px;
  }
  a {
    color: #367bf5;
    font-size: 16px;
    font-weight: 700;
  }
  a:hover {
    text-decoration: none;
  }
  p {
    color: #919699;
  }
}

.outlet-product {
  width: 100%;
  height: 100%;
  padding: 40px 100px;
  background-color: white;
  border-radius: 5px;
  .table td {
    height: 20px;
    vertical-align: middle;
  }
  p {
    font-size: 25px;
    font-weight: 700;
    text-align: center;
    padding-bottom: 40px;
  }
}

.check {
  width: 200px;
  text-align: center;
  input[type='checkbox'] {
    border-radius: 5px;
    appearance: none;
    -webkit-appearance: none;
    width: 18px;
    height: 16px;
    cursor: pointer;
    position: relative;
    padding: 0;
  }
  input[type='checkbox']:checked {
    background-color: #409fd6;
    background: #409fd6
      url('data:image/gif;base64,R0lGODlhCwAKAIABAP////3cnSH5BAEKAAEALAAAAAALAAoAAAIUjH+AC73WHIsw0UCjglraO20PNhYAOw==')
      3px 3px no-repeat;
  }
}
.outlet-list {
  width: 100%;
  td {
    font-size: 14px;
    height: 20px;
    padding: 5px;
    border-bottom: 1px solid #c4c4c4;
    border-top: 1px solid #c4c4c4;
  }
  th {
    padding: 5px;
    .four {
      padding-left: 10px;
    }
  }
}
