.order {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: calc(100% - 220px);
  padding: 40px;
  .order-table::-webkit-scrollbar {
    width: 10px;
    height: 10px;
    background-color: #f5f5f5;
  }
  .order-table::-webkit-scrollbar-thumb {
    border-radius: 5px;
    background-color: #f78a29;
  }
  .order-table {
    width: 100%;
    height: 100%;
    padding: 60px 40px;
    background-color: white;
    border-radius: 5px;
    overflow: auto;
  }
  .table {
    table {
      font-size: 14px;
      border-collapse: collapse;
      width: 100%;
      th {
        font-family: 'Roboto', sans-serif;
        font-weight: 700;
        font-size: 16px;
        border-bottom: 1px solid #c4c4c4;
        .second {
          padding-left: 50px;
        }
        .third {
          padding-left: 20px;
        }
      }
      td {
        height: 40px;
        font-weight: 600;
        vertical-align: middle;
        border-bottom: 1px solid #c4c4c4;
        border-top: 1px solid #c4c4c4;
      }
      .drop-order {
        padding-left: 10px;
        display: inline;
      }
      .undeli {
        font-weight: 700;
        color: #e10916;
      }
      .deli {
        font-weight: 700;
        color: #087a35;
      }
      .edit {
        color: #367bf5;
        text-decoration: underline;
        padding-right: 30px;
      }
      .status {
        padding-left: 30px;
      }
      .action {
        padding-left: 30px;
      }
    }
  }
  .order-header {
    padding-bottom: 50px;
    display: flex;
    align-items: center;
    font-family: 'Roboto', sans-serif;
    @media only screen and (max-width: 1500px) {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
    }
    .order-item1 {
      display: flex;
      flex-wrap: nowrap;
      padding-right: 20px;
      @media only screen and (max-width: 1500px) {
        padding-bottom: 10px;
      }
      .item-a{
        display: flex;
      }
      input {
        width: 250px;
        height: 40px;
        border: 1px solid #333333;
        border-radius: 10px;
        margin-right: 20px;
        padding: 5px 12px;
        font-size: 14px;
      }
    }
    .order-item2 {
      display: flex;
      margin-left: auto;
      align-items: center;
      @media only screen and (max-width: 1500px) {
        width: 100%;
        min-width: 800px;
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        align-items: flex-start;
        .item-a {
          padding-bottom: 10px;
        }
      }
      select {
        appearance: none;
        background: url('../../../img/site_images/dropdown.svg');
        background-repeat: no-repeat;
        background-position: 95%;
        border: 1px solid #333333;
        height: 40px;
      }
    }
  }
}

.drop-product {
  background-color: white;
  font-size: 14px;
  border: 1px solid #333333;
  border-radius: 10px;
  cursor: pointer;
  width: 180px;
  padding: 5px 12px;
  text-align: left;
  height: 32px;
  margin-right: 20px;
}
.drop-product-item1 {
  background-color: white;
  font-size: 14px;
  border: 1px solid #333333;
  border-radius: 10px;
  cursor: pointer;
  width: 140px;
  padding: 5px 12px;
  text-align: left;
  height: 32px;
  margin-right: 10px;
}
.drop-product-item3 {
  background-color: white;
  font-size: 14px;
  border: 1px solid #333333;
  border-radius: 10px;
  cursor: pointer;
  width: 180px;
  padding: 5px 12px;
  text-align: left;
  height: 32px;
  margin-right: 80px;
}

.order-add {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: calc(100% - 220px);
  padding: 60px;
  overflow: auto;
  .add-order::-webkit-scrollbar {
    width: 10px;
    height: 10px;
    background-color: #f5f5f5;
  }
  .add-order::-webkit-scrollbar-thumb {
    border-radius: 5px;
    background-color: #f78a29;
  }
  .add-order {
    width: 100%;
    height: 100%;
    background-color: white;
    border-radius: 5px;
    padding: 40px 130px;
    overflow: auto;
    .title {
      font-size: 25px;
      font-weight: 700;
      text-align: center;
      padding: 40px 0 10px 0;
      min-width: 300px;
    }
    .order-form {
      display: flex;
      flex-wrap: nowrap;
      font-family: 'Roboto', sans-serif;
      padding: 20px;
      .add-left {
        margin-right: 50px;
        width: 50%;
        min-width: 200px;
      }
      .add-right {
        margin-left: 50px;
        width: 50%;
        min-width: 200px;
        select {
          appearance: none;
          background: url('../../../img/site_images/select.svg');
          background-repeat: no-repeat;
          background-position: 95%;
          width: 100%;
          height: 50px;
          border: 1px solid #e3e5e6;
          padding: 10px;
        }
      }
      input {
        width: 100%;
        height: 50px;
        border: 1px solid #e3e5e6;
        border-radius: 8px;
      }
    }
    label {
      font-weight: 700;
      font-size: 16px;
      padding: 20px 0 10px 0;
      font-family: 'Roboto', sans-serif;
      min-width: 200px;
    }
    table {
      text-align: center;
      width: 100%;
      min-width: 500px;
      th {
        padding: 5px 0;
        font-family: 'Roboto', sans-serif;
        font-size: 16px;
      }
      td {
        height: 40px;
        padding: 5px 0;
        font-size: 14px;
        font-weight: 600;
      }
      .s-no {
        text-align: left;
      }
      .total {
        font-family: 'Roboto', sans-serif;
        font-weight: 700;
        font-size: 16px;
        text-decoration: underline;
      }
      .detail {
        font-weight: 700;
      }
    }
  }
}
