.user-container {
  width: 100%;
  display: flex;
  background-color: #e5e5e5;
  min-height: 100vh;
  max-height: -webkit-fill-available;
}
.nav-bar {
  background-color: #252525;
  bottom: 0;
  list-style-type: none;
  font-size: 16px;
  font-weight: 700;
  color: white;
  width: 220px;
  margin: 0;
  padding: 40px 0;

  li {
    padding: 20px 40px;
    font-size: 16px;
    color: white;
    font-weight: 400;
  }
  li:hover {
    background-color: #f78a29;
    color: black;
  }
  a {
    padding: 0;
    text-decoration: none;
  }
  a:hover {
    text-decoration: none;
  }
  .uom {
    display: none;
    padding: 5px 40px;
    font-weight: 700;
  }
  .others:hover {
    .uom {
      display: block;
    }
  }
}

.user {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: calc(100% - 220px);
  padding: 40px;
  overflow: auto;
  .user-table::-webkit-scrollbar {
    width: 10px;
    height: 10px;
    background-color: #f5f5f5;
  }
  .user-table::-webkit-scrollbar-thumb {
    border-radius: 5px;
    background-color: #f78a29;
  }
  .user-table {
    width: 100%;
    height: 100%;
    padding: 60px 40px;
    background-color: white;
    border-radius: 5px;
    overflow: auto;
    .user-header {
      padding-bottom: 50px;
      display: flex;
      align-items: center;
      font-family: 'Roboto', sans-serif;
      @media only screen and (max-width: 1100px) {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
      }
      .user-item1 {
        display: flex;
        padding-right: 20px;
        align-items: center;
        @media only screen and (max-width: 1100px) {
          padding-bottom: 10px;
        }
        input {
          width: 250px;
          border: 1px solid #333333;
          border-radius: 10px;
          margin-right: 20px;
          padding: 8px 12px;
          font-size: 14px;
        }
        font-family: 'Roboto', sans-serif;
      }
      .user-item2 {
        display: flex;
        margin-left: auto;
        align-items: center;
        @media only screen and (max-width: 1100px) {
          width: 100%;
          min-width: 450px;
        }
        select {
          appearance: none;
          background: url('../../../img/site_images/dropdown.svg');
          background-repeat: no-repeat;
          background-position: 95%;
          border: 1px solid #333333;
        }
      }
    }
    .table {
      table {
        font-size: 14px;
        border-collapse: collapse;
        width: 100%;
      }
      th {
        font-size: 16px;
        font-family: 'Roboto', sans-serif;
      }
      td {
        height: 80px;
        vertical-align: middle;
        font-weight: 600;
        border-bottom: 1px solid #c4c4c4;
        border-top: 1px solid #c4c4c4;
      }
      .edit {
        color: #367bf5;
        text-decoration: underline;
        padding-right: 30px;
        font-weight: 700;
      }
      .delete {
        color: #e10916;
        text-decoration: underline;
        font-weight: 700;
      }
      .action {
        padding-left: 40px;
      }
    }
  }
}
.find {
  width: 32px;
  height: 30px;
  border-radius: 5px;
  border: 1px solid #c0caff;
  background-color: #eaedff;
}
.create {
  width: 180px;
  height: 40px;
  background: #ffffff;
  border: 1px solid #f78a29;
  border-radius: 99px;
  font-size: 20px;
  padding: 4px 12px;
  display: flex inline;
  justify-content: center;
  align-items: center;
  font-family: 'Quicksand', sans-serif;
  a {
    color: #f78a29;
    font-size: 20px;
    font-weight: 500;
  }
  a:hover {
    text-decoration: none;
  }
  img {
    margin-right: 10px;
  }
}
.dropbtn {
  background-color: white;
  font-size: 14px;
  border: 1px solid #333333;
  border-radius: 10px;
  cursor: pointer;
  height: 40px;
  padding: 5px 12px;
  text-align: left;

  .drop {
    float: right;
  }
}

.user-add {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: calc(100% - 220px);
  padding: 60px;
  overflow: auto;
  .add-user::-webkit-scrollbar {
    width: 10px;
    height: 10px;
    background-color: #f5f5f5;
  }
  .add-user::-webkit-scrollbar-thumb {
    border-radius: 5px;
    background-color: #f78a29;
  }
  .add-user {
    width: 100%;
    height: 100%;
    background-color: white;
    border-radius: 5px;
    padding: 80px 140px 0;
    overflow: auto;
    .title {
      font-size: 25px;
      font-weight: 700;
      text-align: center;
      padding-bottom: 20px;
      padding-top: 0;
      min-width: 200px;
    }
    .add-form {
      display: flex;
      flex-wrap: nowrap;
      font-family: 'Roboto', sans-serif;
      padding: 20px;
      .add-left {
        margin-right: 50px;
        min-width: 200px;
        width: 50%;
        .role {
          padding-top: 40px;
          select {
            appearance: none;
            background: url('../../../img/site_images/select.svg');
            background-repeat: no-repeat;
            background-position: 95%;
            width: 100%;
            height: 50px;
            border: 1px solid #e3e5e6;
            padding: 10px;
          }
        }
      }
      .add-right {
        margin-left: 50px;
        min-width: 200px;
        width: 50%;
        .facto {
          display: flex;
          align-items: flex-start;
          margin-top: 30px;
          input {
            margin-right: 0;
          }
          label {
            margin-top: -20px;
          }
        }
        .checkbox-contain {
          font-size: 14px;
          font-weight: 400;
          padding-top: 40px;
          width: 290px;
          .checkbox-item {
            display: flex;
            align-items: center;
            padding-bottom: 20px;

            input[type='checkbox'] {
              border: 1px solid #363740;
              border-radius: 5px;
              appearance: none;
              -webkit-appearance: none;
              width: 20px;
              height: 20px;
              cursor: pointer;
              position: relative;
              padding: 0;
            }
            input[type='checkbox']:checked {
              background-color: #409fd6;
              background: #409fd6
                url('data:image/gif;base64,R0lGODlhCwAKAIABAP////3cnSH5BAEKAAEALAAAAAALAAoAAAIUjH+AC73WHIsw0UCjglraO20PNhYAOw==')
                3px 3px no-repeat;
            }
            label {
              padding: 0;
              font-weight: 600;
              font-family: 'Mulish', sans-serif;
              font-size: 14px;
              margin: 0;
            }
          }
        }
      }
      input {
        width: 100%;
        height: 50px;
        border: 1px solid #e3e5e6;
        border-radius: 8px;
      }
      label {
        font-weight: 700;
        font-size: 16px;
        padding: 20px 0 10px 0;
      }
    }
  }
}

.back {
  width: 100%;
  padding-bottom: 20px;
  font-family: 'Roboto', sans-serif;
  a {
    font-size: 16px;
    color: #919699;
    padding-left: 10px;
  }
  a:hover {
    text-decoration: none;
  }
}
