.full-screen {
  height: 100%;
  width: 100%;
  left: 0;
  top: 0;
  overflow: auto;
  position: fixed;
  background-color: #fff;
}
.top-bar {
  display: flex;
  background-color: #f78a29;
  align-items: center;
  justify-content: space-evenly;
  padding: 0 20px;
  width: 100%;
  height: 60px;
  min-width: 900px;
  .top-bar-find {
    display: flex;
    align-items: center;
    position: relative;
    input {
      width: 320px;
      height: 40px;
      background: #ffffff;
      border: 1px solid #f78a29;
      box-sizing: border-box;
      border-radius: 10px;
      font-family: 'Roboto', sans-serif;
    }
  }
  .menu {
    color: #252525;
    font-weight: 700;
    font-size: 16px;
    margin: 0;
    padding-right: 100px;
  }
  .orders {
    color: white;
    font-weight: 700;
    font-size: 16px;
    margin: 0;
    padding: 0 10px;
    min-width: 140px;
    display: inline;
  }
  .icon {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 20px;
    .basket {
      width: 28px;
      height: 28px;
      margin-right: 20px;
    }
    .user-icon {
      width: 28px;
      height: 28px;
    }
  }
}
.main-content {
  padding: 40px;
  display: flex;
  width: 100%;
  margin: 0;
  .left-bar {
    width: 160px;
    height: 290px;
    background: #ffffff;
    border: 1px solid rgba(247, 138, 41, 0.6);
    box-sizing: border-box;
    border-radius: 5px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 20px;
    ul {
      padding: 0;
      li {
        cursor: pointer;
        list-style-type: none;
        padding: 10px 10px;
        font-size: 16px;
        font-weight: 400;
        .menu-icon {
          margin-left: -20px;
          font-weight: 700;
          margin-bottom: 10px;
          img {
            width: 25px;
            height: 25px;
            margin-right: 10px;
          }
        }
      }
      li:hover {
        color: #f78a29;
        font-weight: 700;
      }
    }
  }
  .img-item::-webkit-scrollbar {
    width: 10px;
    height: 10px;
    background-color: #f5f5f5;
  }
  .img-item::-webkit-scrollbar-thumb {
    border-radius: 5px;
    background-color: #f78a29;
  }
  .right-img {
    width: calc(100% - 160px);
    .img-item {
      display: flex;
      margin-left: 40px;
      padding: 20px;
      display: flex;
      overflow: auto;
      .img-container {
        margin-left: auto;
        margin-right: 10px;
        width: 260px;
        padding-bottom: 20px;
        display: flex;
        flex-direction: column;
        img {
          width: 260px;
          height: 260px;
        }
        .btn-add {
          width: 100%;
          button {
            width: 220px;
            height: 42px;
            margin: auto;
            border: 1px solid #000000;
            background-color: #fff;
            display: flex;
            justify-content: center;
            align-items: center;
            font-size: 20px;
            color: #2c2c2c;
            img {
              width: 17px;
              height: 15px;
              margin-right: 10px;
            }
          }
          button:hover {
            background-color: #f78a29;
            border: 1px solid #f78a29;
            color: white;
          }
        }
        input[type='number'] {
          -webkit-appearance: textfield;
          -moz-appearance: textfield;
          appearance: textfield;
        }

        input[type='number']::-webkit-inner-spin-button,
        input[type='number']::-webkit-outer-spin-button {
          -webkit-appearance: none;
        }
        .number-input {
          border: 0;
          display: inline-flex;
          margin: auto;
          .minus {
            border-right: none;
          }
          .plus {
            border-left: none;
          }
          button {
            outline: none;
            -webkit-appearance: none;
            background-color: transparent;
            border: 1px solid #f78a29;
            align-items: center;
            justify-content: center;
            width: 40px;
            height: 42px;
            cursor: pointer;
            margin: 0;
            position: relative;
          }
        }
        .number-input button:before,
        .number-input button:after {
          display: inline-block;
          position: absolute;
          content: '';
          width: 20px;
          height: 1px;
          background-color: #f78a29;
          transform: translate(-50%, -50%);
        }
        .number-input button.plus:after {
          transform: translate(-50%, -50%) rotate(90deg);
        }

        .number-input input[type='number'] {
          width: 140px;
          height: 42px;
          border: none;
          margin: 0;
          font-size: 20px;
          color: #f78a29;
          text-align: center;
          border-radius: 0;
          border: 1px solid #f78a29;

        }
        label {
          margin-top: 10px;
          font-size: 18px;
          color: #2c2c2c;
          padding-left: 10px;
        }
        p {
          color: #333333;
          font-size: 18px;
          font-weight: 700;
          padding-left: 10px;
        }
      }
      .img-container:hover {
        border: 1px solid #bcbcbc;
      }
    }
  }
}
