.statement {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: calc(100% - 220px);
  padding: 40px;
  overflow: auto;
  .statement-table::-webkit-scrollbar {
    width: 10px;
    height: 10px;
    background-color: #f5f5f5;
  }
  .statement-table::-webkit-scrollbar-thumb {
    border-radius: 5px;
    background-color: #f78a29;
  }
  .statement-table {
    width: 100%;
    height: 100%;
    padding: 60px 40px;
    background-color: white;
    border-radius: 5px;
    overflow: auto;
    .table {
      min-width: 700px;
      table {
        font-size: 14px;
        border-collapse: collapse;
        width: 100%;
        th {
          font-family: 'Roboto', sans-serif;
          font-weight: 700;
          font-size: 16px;
          border-bottom: 1px solid #c4c4c4;
          padding: 5px 5px;
        }
        td {
          height: 50px;
          font-weight: 600;
          vertical-align: middle;
          border-bottom: 1px solid #c4c4c4;
          border-top: 1px solid #c4c4c4;
          padding: 5px 0;
        }
        .drop-order {
          padding-left: 10px;
          display: inline;
        }
      }
    }
  }
}

.statement-header {
  padding-bottom: 50px;
  display: flex;
  align-items: center;
  font-family: 'Roboto', sans-serif;
  @media only screen and (max-width: 1500px) {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  .statement-item1 {
    display: flex;
    align-items: center;
    flex-wrap: nowrap;
    padding-right: 20px;
    @media only screen and (max-width: 1500px) {
      padding-bottom: 10px;
    }
    input {
      width: 250px;
      height: 40px;
      border: 1px solid #333333;
      border-radius: 10px;
      margin-right: 20px;
      padding: 5px 12px;
      font-size: 14px;
    }
  }
  .statement-item2 {
    display: flex;
    margin-left: auto;
    align-items: center;
    @media only screen and (max-width: 1500px) {
      width: 100%;
      min-width: 800px;
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      align-items: flex-start;
      .item-a {
        padding-bottom: 10px;
      }
    }
    select {
      height: 40px;
      appearance: none;
      background: url('../../../img/site_images/dropdown.svg');
      background-repeat: no-repeat;
      background-position: 95%;
      border: 1px solid #333333;
    }
  }
  label {
    padding: 0 10px 0 20px;
    margin: 0;
    font-weight: 700;
  }
  .issue {
    width: 125px;
    height: 44px;
    border-radius: 99px;
    border: 1px solid #f78a29;
    background: #ffffff;
    a {
      color: #f78a29;
      font-size: 20px;
      font-family: 'Quicksand', sans-serif;
      font-weight: 700;
    }
    a:hover {
      text-decoration: none;
    }
  }
}

.edit {
  color: #367bf5;
  text-decoration: underline;
  padding-right: 30px;
}
.header {
  font-family: 'Roboto', sans-serif;
  display: flex;
  padding: 40px 0 0 40px;
  h5 {
    font-weight: 700;
    font-size: 20px;
    margin: 0;
  }
  p {
    margin: 0;
  }
  img {
    width: 200px;
    height: 120px;
  }
}
.main-container {
  display: flex;
  padding: 40px;
  font-family: 'Roboto', sans-serif;
  .main-item1 {
    display: flex;
    align-items: center;
    .part1 {
      border-left: 5px solid black;
      padding-right: 20px;
      height: 96px;
      p {
        margin: 0;
        padding-left: 5px;
      }
    }
    .part2 {
      padding: 10px;
      border: 4px solid #e10916;
      p {
        margin: 0;
      }
    }
  }
  .main-item2 {
    h3 {
      font-size: 25px;
      padding-bottom: 30px;
    }
    text-align: center;
    width: 60%;
    margin-left: auto;
  }
  table {
    width: 100%;
    min-width: 400px;
  }
}
.table-container {
  padding: 0 40px;
  font-family: 'Roboto', sans-serif;
  font-size: 14px;
  text-align: left;
  .table-header {
    display: flex;
    align-items: center;
    h3 {
      margin: 14px 0;
      padding-left: 40px;
    }
  }
  table {
    border-collapse: collapse;
    width: 100%;
    border-bottom: 1px solid black;
    th {
      padding: 5px 0;
      border-top: 1px solid black;
      border-bottom: 1px solid black;
    }
    td {
      padding: 5px 0;
    }
    .highlight {
      background-color: yellow;
    }
  }
}
.table-footer {
  padding: 40px 40px 100px 40px;
  font-family: 'Roboto', sans-serif;
  table {
    width: 100%;
    text-align: center;
    border: 2px solid black;
    border-collapse: collapse;
    th,
    td {
      padding: 5px 5px;
      border-top: none;
      border-right: 2px solid black;
    }
    th:last-child,
    td:last-child {
      border: none;
    }
  }
}
