.factory {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: calc(100% - 220px);
  padding: 40px;
  overflow: auto;
}
.factory-table::-webkit-scrollbar {
  width: 10px;
  height: 10px;
  background-color: #f5f5f5;
}
.factory-table::-webkit-scrollbar-thumb {
  border-radius: 5px;
  background-color: #f78a29;
}
.factory-table {
  width: 100%;
  height: 100%;
  padding: 60px 40px;
  background-color: white;
  border-radius: 5px;
  overflow: auto;
  .factory-header {
    padding-bottom: 50px;
    display: flex;
    align-items: center;
    @media only screen and (max-width: 900px) {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
    }
    .factory-item1 {
      display: flex;
      flex-wrap: nowrap;
      padding-right: 20px;
      align-items: center;
      @media only screen and (max-width: 900px) {
        padding-bottom: 10px;
      }
      input {
        width: 230px;
        border: 1px solid #333333;
        border-radius: 10px;
        margin-right: 20px;
        padding: 5px 12px;
        font-size: 14px;
        height: 40px;

      }
    }
    .factory-item2 {
      display: flex;
      margin-left: auto;
      align-items: center;
      @media only screen and (max-width: 900px) {
        width: 100%;
        min-width: 450px;
      }
    }
  }

  .table {
    table {
      font-size: 14px;
      border-collapse: collapse;
      width: 100%;
    }
    td {
      height: 80px;
      vertical-align: middle;
      font-weight: 600;
      border-bottom: 1px solid #c4c4c4;
      border-top: 1px solid #c4c4c4;
    }
    th {
      font-family: 'Roboto', sans-serif;
      font-weight: 700;
      font-size: 16px;
    }
    .first {
      width: 450px;
      padding-left: 20px;
    }
    .second {
      padding-left: 50px;
    }
    .third {
      padding-left: 20px;
    }
    .edit {
      color: #367bf5;
      text-decoration: underline;
      padding-right: 30px;
      font-weight: 700;
    }
    .delete {
      color: #e10916;
      text-decoration: underline;
      font-weight: 700;
    }
  }
}

.factory-header {
  padding-bottom: 50px;
  .factory-item1 {
    float: right;
  }
  .factory-item2 {
    input {
      width: 230px;
      border: 1px solid #333333;
      border-radius: 10px;
      margin-right: 20px;
      padding: 5px 12px;
      font-size: 14px;
    }

    .find {
      width: 35px;
      height: 30px;
      border-radius: 5px;
      border: 1px solid #c0caff;
      background-color: #eaedff;
    }
  }
}
.factory-add {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: calc(100% - 220px);
  padding: 60px;
  overflow: auto;
  .add-factory::-webkit-scrollbar {
    width: 10px;
    height: 10px;
    background-color: #f5f5f5;
  }
  .add-factory::-webkit-scrollbar-thumb {
    border-radius: 5px;
    background-color: #f78a29;
  }
  .add-factory {
    width: 100%;
    background-color: white;
    border-radius: 5px;
    padding: 40px 130px 200px;
    overflow: auto;
    .title {
      font-size: 25px;
      font-weight: 700;
      text-align: center;
      padding: 40px 0 30px 0;
      min-width: 200px;
    }
    .factory-form {
      display: flex;
      flex-direction: column;
      flex-wrap: nowrap;
      font-family: 'Roboto', sans-serif;

      .remarks {
        height: 90px;
      }
      input {
        width: 100%;
        height: 50px;
        border: 1px solid hsl(200, 6%, 90%);
        border-radius: 8px;
        min-width: 200px;
      }
      label {
        font-weight: 700;
        font-size: 16px;
        padding: 20px 0 10px 0;
      }
      .factory-item {
        display: flex;
        flex-wrap: nowrap;
        .item1 {
          margin-right: 50px;
          width: 50%;
          min-width: 200px;
        }
        .item2 {
          margin-left: 50px;
          width: 50%;
          min-width: 200px;
        }
      }
    }
  }
}
