.top-bar {
  min-width: 400px;
  a:hover {
    text-decoration: none;
  }
  .icon-left {
    display: flex;
    .icon-menu {
      margin-right: 10px;
      width: 25px;
      height: 25px;
    }
  }
}
.detail {
  padding: 60px;
  .title {
    text-align: left;
    color: #f78a29;
    margin: 20px 0 60px 0;
    padding: 0;
  }
  .detail-container {
    display: flex;
    p {
      font-weight: 700;
      font-size: 25px;
      line-height: 31px;
      letter-spacing: 0.2px;
      color: #f78a29;
      margin-bottom: 40px;
    }
    .detail-table::-webkit-scrollbar {
      width: 10px;
      height: 10px;
      background-color: #f5f5f5;
    }
    .detail-table::-webkit-scrollbar-thumb {
      border-radius: 5px;
      background-color: #f78a29;
    
    }
    .detail-table {
      overflow: auto;
      width: 100%;
      padding: 0 40px 40px 0;
      table {
        text-align: center;
        min-width: 800px;
        thead {
          tr {
            th {
              padding: 20px;
            }
            .total {
              min-width: 200px;
            }
          }
        }
        .left {
          text-align: left;
        }
        tbody {
          .first {
            font-weight: 700;
            text-decoration: underline;
          }
          .second {
            font-weight: 700;
          }
          td {
            vertical-align: middle;
            padding: 20px;
            .icon-menu {
              display: flex;
              align-items: center;
            }
            img {
              margin-right: 20px;
              width: 100px;
              height: 100px;
            }
          }
        }
      }
      .report-issue {
        border: 1px solid #e8e8e8;
        border-radius: 10px;
        padding: 20px;
        min-width: 800px;
        label {
          font-weight: 700;
          padding: 10px 0;
        }
        .upload {
          img {
            width: 30px;
            height: 30px;
          }
        }
        .issue {
          textarea {
            resize: none;
            width: 100%;
            height: 150px;
          }
        }
      }
    }
    .detail-info {
      width: 370px;
      min-width: 350px;
      height: 240px;
      background: #ffffff;
      border: 1px solid rgba(247, 138, 41, 0.6);
      box-sizing: border-box;
      border-radius: 5px;
      margin-left: 40px;
      padding: 20px;
      .info-head {
        display: flex;
        p {
          font-weight: 700;
          font-size: 16px;
          color: #f78a29;
        }
        img {
          margin-right: 20px;
          width: 30px;
          height: 30px;
        }
      }
      label {
        color: #f78a29;
      }
      p {
        color: black;
        font-size: 16px;
        font-weight: 400;
        .confirm {
          color: #f78a29;
        }
      }
      span {
        font-weight: 700;
      }
    }
  }
}
.footer {
  width: 100%;
  padding: 0 150px;
  display: flex;
  .btnb {
    margin-left: auto;
    min-width: 170px;
  }
  .btna {
    margin-right: 20px;
    min-width: 260px;
  }
  button {
    background: #ffffff;
    border: 1px solid #f78a29;
    box-sizing: border-box;
    border-radius: 20px;
    padding: 5px 10px;
    font-weight: 700;
    font-size: 18px;
    line-height: 23px;
    letter-spacing: 0.2px;
    color: #f78a29;
    img {
      width: 40px;
      height: 30px;
      margin: 0 10px;
    }
    a:hover {
      text-decoration: none;
    }
  }
}
