.bg {
  width: 100%;
  height: 100vh;
  position: absolute;
  top: 0;
  left: 0;
  object-fit: cover;
  pointer-events: none;
}
.logo {
  padding-top: 40px;
  margin-bottom: -10px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  z-index: 1;
  img {
    width: 400px;
    height: 240px;
  }
}
.login-box {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  position: relative;
  z-index: 0.5;
  font-family: 'Roboto', sans-serif;
  .login-form {
    width: 500px;
    height: 350px;
    padding: 30px 50px;
    background-color: white;
    border: 1px solid black;
    display: flex;
    flex-direction: column;
    border-radius: 5px;
    input {
      border: 1px solid #c6cacc;
      border-radius: 5px;
      height: 42px;
      width: 390px;
      padding: 20px;
    }
    label {
      font-size: 14px;
      line-height: 20px;
      margin-top: 10px;
      padding-left: 10px;
    }
    a {
      text-decoration: underline;
      color: black;
      font-size: 14px;
      padding-top: 10px;
      padding-left: 10px;
    }
  }
}

.submit-container {
  display: flex;
  justify-content: center;
  align-items: center;

  input[type='submit'] {
    margin-top: 40px;
    background-color: #f78a29;
    font-size: 16px;
    font-weight: 700;
    border-radius: 8px;
    width: 200px;
    height: 42px;
    border: none;
    padding: 0;
  }
}

.change-pass {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 200px 40px 100px;
  font-family: 'Roboto', sans-serif;
  .letter {
    font-size: 25px;
    font-weight: 700;
    padding-bottom: 40px;
    font-family: 'Mulish', sans-serif;
  }
  .change-form {
    width: 390px;
    display: flex;
    flex-direction: column;
    margin: 0;
    input {
      border: 1px solid #c6cacc;
      border-radius: 5px;
      height: 42px;
      width: 390px;
    }
    .change-btn {
      padding-top: 20px;
      .cancel {
        border: 1px solid #bdbdbd;
        background-color: #bdbdbd;
        border-radius: 8px;
        padding: 10px 40px;
        color: white;
        font-weight: 700;
        width: 140px;
      }
      .change-submit {
        float: right;
        border: 1px solid #f78a29;
        border-radius: 8px;
        background-color: #f78a29;
        padding: 10px 50px;
        font-weight: 700;
        width: 140px;
      }
    }
  }
}

.logo2 {
  padding-top: 100px;
  margin-bottom: -10px;
  display: flex;
  justify-content: center;
  align-items: center;
  img {
    width: 200px;
    height: 122px;
  }
}
.forgot-container {
  display: flex;
  justify-content: center;
  font-family: 'Roboto', sans-serif;
  .forgot-form {
    width: 500px;
    padding: 50px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    input {
      border: 1px solid #c6cacc;
      border-radius: 5px;
      height: 42px;
      width: 390px;
    }
    input[type='submit'] {
      width: 200px;
    }
  }
}

.label-item {
  margin-top: -5px;
  color: #afafaf;
  font-size: 12px;
}
