.material {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: calc(100% - 220px);
  padding: 40px;
  overflow: auto;
  .material-table::-webkit-scrollbar {
    width: 10px;
    height: 10px;
    background-color: #f5f5f5;
  }
  .material-table::-webkit-scrollbar-thumb {
    border-radius: 5px;
    background-color: #f78a29;
  }
  .material-table {
    width: 100%;
    height: 100%;
    padding: 60px 40px;
    background-color: white;
    border-radius: 5px;
    overflow: auto;
  }
  .table {
    table {
      font-size: 14px;
      border-collapse: collapse;
      width: 100%;
      th {
        font-family: 'Roboto', sans-serif;
        font-weight: 700;
        font-size: 16px;
        border-bottom: 1px solid #c4c4c4;
      }
      td {
        height: 80px;
        font-weight: 600;
        vertical-align: middle;
        border-bottom: 1px solid #c4c4c4;
        border-top: 1px solid #c4c4c4;
      }
      .first {
        padding-left: 30px;
      }
      .edit {
        color: #367bf5;
        text-decoration: underline;
        font-weight: 700;
        padding-left: 10px;
      }
    }
  }

  .material-header {
    padding-bottom: 50px;
    display: flex;
    align-items: center;
    font-family: 'Roboto', sans-serif;
    @media only screen and (max-width: 1100px) {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
    }
    .material-item1 {
      display: flex;
      flex-wrap: nowrap;
      padding-right: 20px;
      @media only screen and (max-width: 1100px) {
        padding-bottom: 10px;
      }
      input {
        width: 230px;
        border: 1px solid #333333;
        border-radius: 10px;
        margin-right: 20px;
        padding: 5px 12px;
        font-size: 14px;
        height: 40px;
      }
    }
    .material-item2 {
      display: flex;
      margin-left: auto;
      align-items: center;
      @media only screen and (max-width: 1100px) {
        width: 100%;
        min-width: 450px;
      }
      select {
        appearance: none;
        background: url('../../../img/site_images/dropdown.svg');
        background-repeat: no-repeat;
        background-position: 95%;
        border: 1px solid #333333;
      }
    }
  }
}

.material-add {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: calc(100% - 220px);
  padding: 60px;
  overflow: auto;
  .add-material::-webkit-scrollbar {
    width: 10px;
    height: 10px;
    background-color: #f5f5f5;
  }
  .add-material::-webkit-scrollbar-thumb {
    border-radius: 5px;
    background-color: #f78a29;
  }
  .add-material {
    width: 100%;
    height: 100%;
    background-color: white;
    border-radius: 5px;
    padding: 40px 130px;
    overflow: auto;
    .material-form {
      display: flex;
      flex-direction: column;
      flex-wrap: nowrap;
      font-family: 'Roboto', sans-serif;
      label {
        font-weight: 700;
        padding: 10px 0;
        min-width: 300px;
      }
      input {
        width: 100%;
        height: 50px;
        border: 1px solid #e3e5e6;
        padding: 10px;
        min-width: 200px;
      }
      select {
        appearance: none;
        background: url('../../../img/site_images/select.svg');
        background-repeat: no-repeat;
        background-position: 95%;
        width: 100%;
        height: 50px;
        border: 1px solid #e3e5e6;
        padding: 10px;
      }
      .remarks {
        height: 90px;
      }
      .material-item {
        display: flex;
        flex-wrap: nowrap;
        .first-item {
          margin-right: 50px;
          width: 50%;
          min-width: 200px;
        }
        .last-item {
          margin-left: 50px;
          width: 50%;
          min-width: 200px;
        }
      }
    }
    .material-save {
      width: 160px;
      height: 42px;
      background-color: #f78a29;
      border: none;
      border-radius: 8px;
      color: white;
      font-weight: 700;
      margin-top: 120px;
    }
  }
}

.title {
  font-size: 25px;
  font-weight: 700;
  text-align: center;
  padding: 40px 0 30px 0;
  min-width: 200px;
}
