.bg-outlet {
  width: 100%;
  height: 100vh;
  position: absolute;
  top: 0;
  left: 0;
  object-fit: cover;
  pointer-events: none;
}
.login-outlet {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  z-index: 0.5;
  font-family: 'Roboto', sans-serif;
  .login-form {
    width: 500px;
    height: 350px;
    padding: 30px 50px;
    background-color: white;
    border: 1px solid black;
    display: flex;
    flex-direction: column;
    border-radius: 5px;
    input {
      border: 1px solid #c6cacc;
      border-radius: 5px;
      height: 42px;
      width: 390px;
      padding: 20px;
    }
    label {
      font-size: 14px;
      line-height: 20px;
      margin-top: 10px;
      padding-left: 10px;
    }
    a {
      text-decoration: underline;
      color: black;
      font-size: 14px;
      padding-top: 10px;
      padding-left: 10px;
    }
  }
}
