.inventory {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: calc(100% - 220px);
  padding: 40px;
  overflow: auto;
  .inventory-table::-webkit-scrollbar {
    width: 10px;
    height: 10px;
    background-color: #f5f5f5;
  }
  .inventory-table::-webkit-scrollbar-thumb {
    border-radius: 5px;
    background-color: #f78a29;
  }
  .inventory-table {
    width: 100%;
    height: 100%;
    padding: 60px 40px;
    background-color: white;
    border-radius: 5px;
    overflow: auto;
    .table {
      .center {
        padding-left: 20px;
      }
      th {
        height: 30px;
        padding: 5px 10px;
        font-family: 'Roboto', sans-serif;
        font-weight: 700;
        font-size: 16px;
        border-bottom: 1px solid #c4c4c4;
      }
      td {
        height: 40px;
        font-weight: 600;
        vertical-align: middle;
        border-bottom: 1px solid #c4c4c4;
        border-top: 1px solid #c4c4c4;
      }
      table {
        font-size: 14px;
        border-collapse: collapse;
        width: 100%;
        min-width: 500px;
      }
    }
  }
}
.inventory-header {
  padding-bottom: 50px;
  display: flex;
  align-items: center;
  font-family: 'Roboto', sans-serif;
  input {
    width: 250px;
    height: 40px;
  }
  @media only screen and (max-width: 1100px) {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    input {
      margin-bottom: 10px;
    }
  }
  .inventory-item1 {
    display: flex;
    align-items: center;
    flex-wrap: nowrap;
    padding-right: 20px;
    @media only screen and (max-width: 1100px) {
      padding-bottom: 10px;
    }
    input {
      width: 250px;
      height: 40px;
      border: 1px solid #333333;
      border-radius: 10px;
      margin-right: 20px;
      padding: 5px 12px;
      font-size: 14px;
    }
  }
  .inventory-item2 {
    display: flex;
    margin-left: auto;
    align-items: center;
    font-family: 'Quicksand', sans-serif;
    @media only screen and (max-width: 1100px) {
      width: 100%;
      min-width: 450px;
    }
    .daily-product {
      width: 170px;
      height: 40px;
      background: #ffffff;
      border-radius: 5px;
      border: 1px solid #f78a29;
      margin-right: 60px;
      a {
        color: #f78a29;
        font-weight: 700;
      }
    }
    .create-invent {
      width: 185px;
      height: 40px;
      background: #ffffff;
      border: 1px solid #f78a29;
      border-radius: 99px;

      font-size: 20px;
      padding: 4px 12px;
      display: flex inline;
      justify-content: center;
      align-items: center;
      font-weight: 500;
      a {
        color: #f78a29;
      }
      img {
        margin-right: 5px;
      }
    }
    a:hover {
      text-decoration: none;
    }
  }
}

.edit {
  color: #367bf5;
  text-decoration: underline;
  padding-right: 30px;
}

.inventory-tab {
  width: 100%;
  height: 100%;
  padding: 20px 40px;
  background-color: white;
  border-radius: 5px;
  input[type='date']::-webkit-inner-spin-button,
  input[type='date']::-webkit-calendar-picker-indicator {
    display: none;
    -webkit-appearance: none;
  }
  .date {
    appearance: none;
    background: url('../../../img/site_images/Date.svg');
    background-repeat: no-repeat;
    background-position: 95%;
  }
  .inventory-mid {
    display: flex;
    padding: 0 0 20px 20px;
    font-family: 'Roboto', sans-serif;
    font-size: 20px;
    @media only screen and (max-width: 900px) {
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
    }
    span {
      font-weight: 700;
    }
    .mid1 {
      padding-right: 100px;
    }
  }
  .title {
    font-size: 25px;
    font-weight: 700;
    text-align: center;
    padding-bottom: 40px;
  }
  .table-tab::-webkit-scrollbar {
    width: 10px;
    height: 10px;
    background-color: #f5f5f5;
  }
  .table-tab::-webkit-scrollbar-thumb {
    border-radius: 5px;
    background-color: #f78a29;
  }
  .table-tab {
    overflow: auto;
    table {
      font-size: 14px;
      border-collapse: collapse;
      width: 100%;
      min-width: 600px;
      .center {
        padding-left: 20px;
      }
      th {
        height: 30px;
        padding: 5px 10px;
        font-family: 'Roboto', sans-serif;
        font-weight: 700;
        font-size: 16px;
        border-bottom: 1px solid #c4c4c4;
        border-top: 1px solid #c4c4c4;
      }
      td {
        height: 50px;
        font-weight: 600;
        vertical-align: middle;
        border-bottom: 1px solid #c4c4c4;
        padding: 5px 10px;
      }
      .first {
        padding-left: 20px;
        width: 25%;
      }
    }
  }
  .table-inven::-webkit-scrollbar {
    width: 10px;
    height: 10px;
    background-color: #f5f5f5;
  }
  .table-inven::-webkit-scrollbar-thumb {
    border-radius: 5px;
    background-color: #f78a29;
  }
  .table-inven {
    overflow: auto;
    th {
      height: 30px;
      padding: 10px 10px;
      border-bottom: 1px solid #c4c4c4;
      border-top: 1px solid #c4c4c4;
    }
    .quanti {
      input {
        width: 70px;
        text-align: center;
        border: 1px solid #e3e5e6;
      }
    }
    .manu {
      input {
        width: 145px;
        border: 1px solid #e3e5e6;
      }
    }
    .th-first {
      padding-left: 50px;
    }
    .td-first {
      input {
        width: 260px;
        border: 1px solid #e3e5e6;
      }
    }
    .second {
      text-align: center;
    }
    td {
      height: 80px;
      padding: 10px 10px;
      vertical-align: middle;
      border-bottom: 1px solid #c4c4c4;
      input {
        height: 38px;
      }
    }
    .drop-order {
      padding-left: 10px;
      display: inline;
    }
    table {
      font-size: 14px;
      border-collapse: collapse;
      width: 100%;
    }
  }
  label {
    font-family: 'Roboto', sans-serif;
    font-weight: 700;
    padding: 20px 0 10px 10px;
  }
  .btn {
    width: 100%;
    padding-top: 60px;
    .save {
      width: 160px;
      height: 42px;
      background-color: #f78a29;
      border: none;
      border-radius: 8px;
      color: white;
      font-weight: 700;
      a:hover {
        text-decoration: none;
      }
    }
  }
  .table {
    table {
      font-size: 14px;
      border-collapse: collapse;
      width: 100%;
      .one {
        padding-left: 50px;
      }
      th {
        font-size: 16px;
        font-family: 'Roboto', sans-serif;
      }
      td {
        height: 80px;
        vertical-align: middle;
        font-weight: 600;
      }
      .second {
        text-align: center;
      }

      .print {
        color: #f78a29;
        text-decoration: underline;
        font-weight: 700;
      }
    }
  }
}
